	module.exports = {"pathContext":{"group":[{"node":{"title":"親指シフトを極める","id":"c46W6F2LB9KK48AoKWGKQWy","slug":"master_thumb_shift_typing","publishDate":"February 12, 2018","heroImage":{"title":"tes","sizes":{"aspectRatio":1.4988290398126465,"src":"//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=1800&q=50","srcSet":"//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=450&h=300&q=50 450w,\n//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=900&h=600&q=50 900w,\n//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=1280&h=854&q=50 1280w","srcWebp":"//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=1800&q=50&fm=webp","srcSetWebp":"//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=450&h=300&q=50&fm=webp 450w,\n//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=900&h=600&q=50&fm=webp 900w,\n//images.ctfassets.net/ocascwbi08oj/2oKiRRo1b22wkwukmCaCsa/7882505fde41fb2046542066acc3e64a/tes.jpg?w=1280&h=854&q=50&fm=webp 1280w","sizes":"(max-width: 1800px) 100vw, 1800px"}},"body":{"childMarkdownRemark":{"html":"<p>久々にブログなんて始めてしまいました。</p>\n<p>動機はいろいろとあるのですが、最近親指シフトというタイピングに興味があってですね。存在は前から知ってたのですが、その練習にもちょうど良いかなとか思ってようやく重い腰が上がった感じ。</p>\n<p>今は当たり前のように使ってるvimだけど、これも始めた当初はほんとしんどかった。もはや現状vim無しではやってらんねーので、親指シフトもそれくらいの境地に達するのかもという期待を込めて。。。とかいいながらこの文章はローマ字入力してます。エブリデイ明日から本気出す系のしんどさですね！</p>\n<p>親指シフト打つだけならアメブロでもなんでもよいのですが、そこはちょっと頑張ってgatsbyjs+contentful+netlifyとかなんちゃらな、そこそこフレッシュなサービスコンボで挑む所存です。このサイトも<a href=\"https://github.com/ryanwiemer/gatsby-starter-gcn\">これ</a>ベースでフルスクラッチというわけにはいかんけど、カスタマイズしたときの技術メモなんかも記録していきたい。</p>\n<p>もしも検索で来られた方へ::</p>\n<p>よろしければ<a href=\"https://twitter.com/yamasan_hamesan\">フォロー</a>してください！のちのちソーシャルボタンなんかも充実させていきたいですね～</p>\n<p>ほいでは。</p>","excerpt":"…"}}}}],"pathPrefix":"","first":true,"last":true,"index":1,"pageCount":1,"additionalContext":{}}}

